import Vue from 'vue';
//APP可在在非联网状态下使用，
// 而且每个用户都可以自由操作数据，由于这种特殊性，
// 考虑到用户数量较少且数据量较大，
// 所以最终选择使用websql数据库来实现大量数据的存储。
//WebSQL是前端的一个独立模块，是web存储方式的一种
//在WebSql.js中定义数据库
const WebSql = function () {
  if (window.openDatabase) {
//openDatabase('数据库名称','版本','数据库描述','数据库大小')
    let db = openDatabase('mydb', '1.0', 'TextDb', 200 * 1024 * 1024);
    //把链接挂载在Vue的prototype上就可以进行全局调用了
    Vue.prototype.$db = db;
    if (!db) {
      console.log("数据库创建失败");
    } else {
      console.log("数据库创建成功")
    }
  }
};
export default WebSql;



