<template>
  <div class="oldSailDiv">

    <!--      首页条-->
    <index-header></index-header>

    <!--    网页主体-->
    <el-row style="width: 80%;margin: 0 auto;padding-top: 80px;margin-bottom: 40px;">

      <el-col>
        <h2>闲置卖钱：</h2>
        <el-card shadow="always">
          <el-form label-position="top" label-width="80px" :model="submitGoodsForm" :rules="rules"
                   ref="submitGoodsForm">
            <el-col :span="18" :offset="2">
              <el-form-item label="商品图" enctype="multipart/form-data">
                <!--图片上传组件-->
                <el-upload
                  list-type="picture-card"
                  ref="upload"
                  :action="action"
                  :on-success="submitSuccess"
                  :on-error="submitError"
                  :auto-upload="false"
                  :limit="1"
                  multiple
                  :on-exceed="submitIndexOut"
                  accept="image/*">
                  <i slot="default" class="el-icon-plus"></i>
                  <div slot="file" slot-scope="{file}">
                    <img
                      id="img"
                      class="el-upload-list__item-thumbnail"
                      :src="file.url" alt="请检查网络连接"
                    >
                    <span class="el-upload-list__item-actions">
        <span
          class="el-upload-list__item-preview"
          @click="handlePictureCardPreview(file)"
        >
          <i class="el-icon-zoom-in"></i>
        </span>
      </span>
                  </div>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                  <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
                <!--                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器-->
                <!--                </el-button>-->
                <!--                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
              </el-form-item>
            </el-col>
            <el-col :span="9" :offset="2" style="margin-top: 50px;">
              <el-form-item label="商品名称" prop="name">
                <el-input v-model="submitGoodsForm.name" maxlength="25" show-word-limit
                          ref="submitGoodsForm"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9" :offset="2" style="margin-top: 50px;">
              <el-form-item label="商品价格" prop="price">
                <el-input v-model="submitGoodsForm.price"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9" :offset="2" style="margin-top: 20px;">
              <el-form-item label="图书分类" prop="goodsLevel">
                <el-select v-model="value" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="9" :offset="2" style="margin-top: 20px;">
              <el-form-item label="商品描述" prop="dscrip">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 8}"
                  placeholder="请输入内容"
                  v-model="submitGoodsForm.dscrip"
                  maxlength="100"
                  show-word-limit>
                </el-input>
              </el-form-item>
            </el-col>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="5" :offset="9">
        <el-button type="primary" @click="submitForm()" style="width: 100%;">保存</el-button>
      </el-col>
    </el-row>

    <!--      弹出提示框-->
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="clickButton" style="margin-top: 30px;">确 定</el-button>
          </span>
      </div>
    </el-dialog>

    <!--      首页尾-->
    <index-footer></index-footer>
  </div>
</template>

<script>
  import IndexHeader from './pages/index-header'
  import IndexFooter from './pages/index-footer'

  export default {
    name: "oldSail",
    data() {
      let checkPrice = (rule, value, callback) => {
        let numberRex = /^([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])$/;
        if (value === "") {
          return callback(new Error('商品价格不能为空'));
        } else {
          if (!numberRex.test(value)) {
            callback(new Error('请输入正数'));
          } else {
            if (value < 0) {
              callback(new Error('必须大于 0'));
            } else {
              callback();
            }
          }
        }
      };
      let checkGoodsLevel = (rule, value, callback) => {
        let numberRex = /^[1-9]\d*$/;
        if (value === "") {
          return callback(new Error('商品新旧程度不能为空'));
        } else {
          if (!numberRex.test(value)) {
            callback(new Error('请输入正整数'));
          } else {
            if (value < 1 || value > 99) {
              callback(new Error('必须在 1 - 99 之间'));
            } else {
              callback();
            }
          }
        }
      };
      let checkString = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请填写内容'));
        } else {
          callback();
        }
      };
      return {
        options: [{
          value: '选项1',
          label: '热门精品'
        }, {
          value: '选项2',
          label: '低价好物'
        }, {
          value: '选项3',
          label: '9新以上'
        }],
        value: "",
        submitGoodsForm: {
          name: "",
          price: "",
          goodsLevel: "",
          dscrip: "",
          userId: "",
          goodsId: "",
        },
        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false,
        dialogValue: '',
        centerDialogVisible: false,
        submitFlag: false,
        action: "",
        rules: {
          name: [
            {validator: checkString, trigger: 'blur'}
          ],
          price: [
            {validator: checkPrice, trigger: 'blur'}
          ],
          goodsLevel: [
            {validator: checkGoodsLevel, trigger: 'blur'}
          ],
          dscrip: [
            {validator: checkString, trigger: 'blur'}
          ]
        },
        coverImg: "",
      }
    },
    components: {
      IndexHeader, IndexFooter
    },
    mounted() {
      //先从服务端获取一个随机不重复的商品ID作为准备新上传的闲置商品ID
      let self = this;
      $.get("http://localhost:8083/goods/getRandomGoodsId.do", function (data) {
        self.submitGoodsForm.goodsId = data.goodsId;
        //设置上传图片路径，需要一个商品ID
        self.action = "http://localhost:8083/upload/uploadFile.do?goodsId=" + data.goodsId;
      }, "json");
    },
    methods: {
      submitForm() {
        if (this.submitGoodsForm.dscrip === "" || this.submitGoodsForm.price === "" || this.submitGoodsForm.name === "" || this.value === "" || this.getBase64(document.getElementById("img")) === "undefined") {
          console.log("为空");
          return;
        }
        let _this = this;
        console.log("type"+_this.value)
        console.log(_this.getBase64(document.getElementById("img")).toString());
        this.$db.transaction(function (tx) {
          tx.executeSql('INSERT INTO BOOKS(bookId,bookImg,bookName,bookPrice,bookDescribe,bookCount,type) ' +
            'VALUES (?,?,?,?,?,?,?)', [Math.ceil(Math.random() * 10000000), _this.getBase64(document.getElementById("img")), _this.submitGoodsForm.name, _this.submitGoodsForm.price, _this.submitGoodsForm.dscrip, 2, _this.value === "选项1" ? 1 : (_this.value === "选项2" ? 2 : 3)], function () {
            console.log("插入数据成功");
            _this.centerDialogVisible = true;
            _this.dialogValue = "保存成功!";
          }, function () {
            console.log("插入数据失败");
          });
        });

      },
      clickButton() {
        this.centerDialogVisible = false;
        this.$router.push("/");
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      submitSuccess() {
        // console.log("成功")
        this.dialogValue = "已上传";
        this.centerDialogVisible = true;
        this.submitFlag = true;
      },
      submitError() {
        // console.log("失败")
        this.dialogValue = "图片上传失败，请检查网络连接";
        this.centerDialogVisible = true;
        this.submitFlag = false;
      },
      submitIndexOut() {
        this.dialogValue = "图片数量超限";
        this.centerDialogVisible = true;
      },
      changeCoverImg: function (e) {
        console.log(e);
        let file = e.target.files[0]
        let reader = new FileReader()
        let that = this
        reader.readAsDataURL(file)
        reader.onload = function (e) {
          that.coverImg = this.result
          console.log(that.coverImg)
        }
      },
      getBase64(img) {
        console.log(img === null);
        if (img === null) {
          return "undefined";
        }
        let canvas = document.createElement("canvas");
        canvas.width = img.width;
        canvas.height = img.height;
        let ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0, img.width, img.height);
        let ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
        let dataUrl = canvas.toDataURL("images/" + ext);
        return dataUrl;
      }
    }
  }
</script>

<style scoped>
  h2 {
    font-weight: 400;
    color: #1f2f3d;
    font-size: 28px;
  }
</style>



