<template>
  <div class="personInfo" id="personInfoId">
    <h2>个人信息：</h2>
    <el-card shadow="always">
      <el-form label-position="top" label-width="80px" :rules="rules">
        <el-row :gutter="100">
          <el-col :span="9" :offset="2">
            <el-form-item label="用户名">
              <el-input v-model="personMessageContent.account" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9" :offset="1">
            <el-form-item label="密码" prop="password">
              <el-input v-model="personMessageContent.password" type="password" autocomplete="off"
                        show-password></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="100">
          <el-col :span="9" :offset="2">
            <el-form-item label="用户昵称">
              <el-input v-model="personMessageContent.nickName" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9" :offset="1">
            <el-form-item label="性别">
              <el-select placeholder="还没有性别" style="width: 100%;">
                <el-option label="男"></el-option>
                <el-option label="女"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="100">
          <el-col :span="9" :offset="2">
            <el-form-item label="邮箱">
              <el-input v-model="personMessageContent.mail" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9" :offset="1">
            <el-form-item label="账户验证状态">
              <el-input v-model="personMessageContent.mail" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="margin-top: 50px;">
          <el-col :span="5" :offset="9">
            <el-button type="primary" @click="submitForm()" style="width: 100%;">保存</el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-card>

    <!--      弹出提示框-->
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="clickButton" style="margin-top: 30px;">确 定</el-button>
          </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "person-info",
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          //6到30位数字、字母、特殊符号
          let passwordRex = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[~!@#$%^&*()_+`\-={}:";'<>?,.\/]).{6,30}$/;
          if (!passwordRex.test()) {
            callback(new Error('6到30位包含数字、字母、特殊符号'));
          }
          callback();
        }
      };
      return {
        rules: {
          password: [
            {validator: validatePass, trigger: 'blur'}
          ]
        },
        dialogValue: '',
        centerDialogVisible: false,
        personMessageContent: "",
      }
    },
    mounted() {
      this.personMessage();
    },
    methods: {
      personMessage() {
        let self = this;
        this.$db.transaction(function (tx) {
          // 查找名字是登录的
          // tx.executeSql('SELECT * FROM USER where account="' + self.username`"`, [], function (tx, results) {
          tx.executeSql('SELECT * FROM USER', [], function (tx, results) {
            self.personMessageContent = results.rows.item(0);
            console.log(self.personMessageContent);
          });//将数组赋值给vue创建的数组

        })
      },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
        });
      },
      clickButton() {
        this.centerDialogVisible = false;
      }
    }
  }
</script>

<style scoped>
  .personInfo {
    width: 80%;
    margin: 0 auto;
    padding-top: 80px;
  }
</style>



