<template>
  <div class="personOldDiv" id="personOldId">
    <h2>我的闲置：</h2>
    <!--数据列表-->
    <!--根据商品名搜索-->
    <table style="display: block;padding: 10px 10px">
      <tr>
        <th>商品图片</th>
        <th>商品名称</th>
        <th>商品价格</th>
        <th>商品分类</th>
      </tr>
      <tr v-for="(item,index) in myEmptyBookList">
        <td><img :src="item.bookImg" style="width: 40px"></td>
        <td>{{item.bookName}}</td>
        <td>{{item.bookPrice}}</td>
        <td>{{item.type===1?"热门精品":(item.type===2?"低价好物":"9新以上")}}</td>
      </tr>
    </table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length">
    </el-pagination>


    <!--      商品介绍弹出框-->
    <el-dialog
      title="商品介绍"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <el-form :model="goodsForm">
          <el-form-item label="商品ID" label-position="top">
            <el-input v-model="goodsForm.goodsId" autocomplete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="商品名称" label-position="top">
            <el-input v-model="goodsForm.name" autocomplete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="商品介绍" label-position="top">
            <el-input
              type="textarea"
              placeholder="请输入商品介绍"
              v-model="goodsForm.dscrip"
              maxlength="250"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submitDscrip" style="margin-top: 30px;">保存</el-button>
        </span>
      </div>
    </el-dialog>

    <!--      弹出提示框-->
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible2"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="clickButton" style="margin-top: 30px;">确 定</el-button>
        </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "person-old",
    data() {
      return {
        tableData: [],
        search: '',
        currentPage: 1, //初始页
        pageSize: 5, //每页的数据
        goodsForm: {
          goodsId: "",
          name: "",
          dscrip: "",
        },
        centerDialogVisible: false,
        centerDialogVisible2: false,
        dialogValue: "",
        myEmptyBookList: [],
      }
    },
    mounted() {
      this.myEmptyBook();
    },
    methods: {
      //我的闲置
      myEmptyBook() {
        let self = this;
        this.$db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM BOOKS', [], function (tx, results) {
            let len = results.rows.length, i;
            for (i = 0; i < len; i++) {//把查出来的数据封装到一个对象里面 最后放到数组里面
              let BookId = results.rows.item(i).bookId;
              let BookImg = results.rows.item(i).bookImg;
              let BookName = results.rows.item(i).bookName;
              let BookPrice = results.rows.item(i).bookPrice;
              let BookDescribe = results.rows.item(i).bookDescribe;
              let BookCount = results.rows.item(i).bookCount;
              let Type = results.rows.item(i).type;
              let book = {};//创建一个js对象
              book.bookId = BookId;
              book.bookImg = BookImg;
              book.bookName = BookName;
              book.bookPrice = BookPrice;
              book.bookDescribe = BookDescribe;
              book.bookCount = BookCount;
              book.type = Type;
              self.myEmptyBookList.push(book)
            }
          });

        })
      },
      // 初始页currentPage、初始每页数据数pageSize和数据data
      handleSizeChange: function (size) {
        this.pageSize = size;
        // console.log(this.pageSize)  //每页下拉显示数据
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        // console.log(this.currentPage)  //点击第几页
      },
      handleEdit(index, row) {
        //点击了商品介绍按钮
        // console.log(index, row);
        let jsonObj = {};
        jsonObj.goodsId = row.goodsId;
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        $.get("http://localhost:8083/goods/getGoodsDscrip.do", jsonObj, function (data) {
          if (data !== "" || data !== null) {
            self.goodsForm.goodsId = data.goodsId;
            self.goodsForm.name = data.name;
            self.goodsForm.dscrip = data.dscrip;
            self.centerDialogVisible = true;
          }
        }, "json");
      },
      handleEdit2(index, row) {
        //点击了取消出售按钮
        // console.log(index, row);
        let jsonObj = {};
        jsonObj.goodsId = row.goodsId;
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        $.post("http://localhost:8083/goods/deleteMyGoodsAndUrls.do", jsonMsg, function (data) {
          if (data.code === 1) {
            self.dialogValue = "删除成功";
          } else {
            self.dialogValue = "删除失败，错误码：" + data.code;
          }
          self.centerDialogVisible2 = true;
        }, "json");
        window.location.reload();
      },
      submitDscrip() {
        //点击了保存按钮
        let jsonObj = {};
        jsonObj.goodsId = this.goodsForm.goodsId;
        jsonObj.dscrip = this.goodsForm.dscrip;
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        $.post("http://localhost:8083/goods/saveGoodsDscrip.do", jsonMsg, function (data) {
          if (data.code === 1) {
            self.dialogValue = "保存成功";
          } else {
            self.dialogValue = "保存失败，错误码：" + data.code;
          }
          self.centerDialogVisible2 = true;
        }, "json");
        window.location.reload();
      },
      clickButton() {
        this.centerDialogVisible2 = false;
        this.centerDialogVisible = false;
      }
    }
  }
</script>

<style scoped>
  .personOldDiv {
    width: 80%;
    margin: 0 auto;
    padding-top: 50px;
  }

  tr {
    width: 100%;
  }

  th, td {
    text-align: center;
    width: 25%;
    padding: 10px 20px;
  }
</style>



