<template>
  <div class="ordersDiv" id="ordersDivId">
    <h2>我的订单：</h2>
    <!--数据列表-->
    <!--根据商品名搜索-->
    <table style="display: block;padding: 10px 10px">
      <tr>
        <th>商品图片</th>
        <th>商品Id</th>
        <th>商品名称</th>
        <th>商品价格</th>
      </tr>
      <tr v-for="(item,index) in shopCarList">
        <td><img :src="item.shopCarImg" style="width: 40px"></td>
        <td>{{item.shopCarId}}</td>
        <td>{{item.shopCarName}}</td>
        <td>{{item.shopCarPrice}}</td>
      </tr>
    </table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length">
    </el-pagination>

    <!--      反馈弹出框-->
    <el-dialog
      title="反馈"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <el-form :model="feedbackForm">
          <el-form-item label="订单ID" label-position="top">
            <el-input v-model="feedbackForm.ordersId" autocomplete="off" disabled></el-input>
          </el-form-item>
          <el-form-item label="反馈类型" label-position="top">
            <el-select v-model="feedbackForm.feedbacksStatus" placeholder="请选择反馈类型" style="width: 100%;">
              <el-option label="投诉" :value=1></el-option>
              <el-option label="建议" :value=0></el-option>
              <el-option label="其他" :value=-1></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="反馈内容" label-position="top">
            <el-input
              type="textarea"
              placeholder="请输入反馈内容"
              v-model="feedbackForm.value"
              maxlength="250"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="submitFeedback" style="margin-top: 30px;">提交反馈</el-button>
          </span>
      </div>
    </el-dialog>
    <!--      反馈结果提示框-->
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible2"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="clickButton" style="margin-top: 30px;">确 定</el-button>
          </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "person-orders",
    data() {
      return {
        tableData: [],
        search: '',
        centerDialogVisible: false,
        centerDialogVisible2: false,
        feedbackForm: {
          ordersId: "",
          feedbacksStatus: -1,
          value: "",
          userId: "",
          goodsId: "",
          payId: "",
          feedbacksTime: "",
          solveStatus: 0,
          solveValue: "",
          name: ""
        },
        dialogValue: '',
        currentPage: 1, //初始页
        pageSize: 5, //每页的数据
        shopCarList:[],
      }
    },
    mounted() {
      this.showShopCar();
    },
    methods: {
      //显示购物车
      showShopCar() {
        let self = this;
        this.$db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM SHOPCAR', [], function (tx, results) {
            let len = results.rows.length;
            // console.log(len);
            // console.log(results.rows);
            for (let i = 0; i < len; i++) {//把查出来的数据封装到一个对象里面 最后放到数组里面
              let shopCarId = results.rows.item(i).shopCarId;
              let shopCarImg = results.rows.item(i).shopCarImg;
              let shopCarPrice = results.rows.item(i).shopCarPrice;
              let shopCarName = results.rows.item(i).shopCarName;
              let shopCarBook = {};//创建一个js对象
              shopCarBook.shopCarId = shopCarId;
              shopCarBook.shopCarImg = shopCarImg;
              shopCarBook.shopCarPrice = shopCarPrice;
              shopCarBook.shopCarName = shopCarName;
              self.shopCarList.push(shopCarBook)
            }
            console.log(self.shopCarList)

          });//将数组赋值给vue创建的数组

        })
      },

      handleEdit(index, row) {
        //点击了反馈按钮
        // console.log(index, row);
        this.feedbackForm.ordersId = row.ordersId;
        this.centerDialogVisible = true;
        this.feedbackForm.feedbacksStatus = -1;
        this.feedbackForm.value = "";
        this.feedbackForm.userId = window.sessionStorage.getItem("userId");
        this.feedbackForm.name = row.name;
        this.feedbackForm.feedbacksTime = new Date().toLocaleString();
        //模拟支付
        this.feedbackForm.payId = row.goodsId;
        this.feedbackForm.goodsId = row.goodsId;
      },
      submitFeedback() {
        // console.log("点击了提交反馈");
        let self = this;
        let jsonMsg = JSON.stringify(this.feedbackForm);
        $.post("http://localhost:8083/feedback/submitFeedback.do", jsonMsg, function (data) {
          if (data.code === 1) {
            self.dialogValue = "提交成功";
          } else {
            self.dialogValue = "提交失败，错误码" + data.code;
          }
          self.centerDialogVisible2 = true;
        }, "json");
      },
      clickButton() {
        this.centerDialogVisible2 = false;
        if (this.dialogValue === "提交成功") {
          this.centerDialogVisible = false;
        }
      },
      // 初始页currentPage、初始每页数据数pageSize和数据data
      handleSizeChange: function (size) {
        this.pageSize = size;
        // console.log(this.pageSize)  //每页下拉显示数据
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        // console.log(this.currentPage)  //点击第几页
      },
    }
  }
</script>

<style scoped>
  .ordersDiv {
    width: 80%;
    margin: 0 auto;
    padding-top: 50px;
  }

  tr {
    width: 100%;
  }

  th, td {
    text-align: center;
    width: 25%;
    padding: 10px 20px;
  }
</style>



