<template>
  <div class="index">
    <!--首页头-->
    <index-header></index-header>
    <!--首页 Logo-->
    <index-logo></index-logo>
    <!--首页主体-->
    <index-body></index-body>
    <!--首页尾-->
    <index-footer></index-footer>
  </div>
</template>

<script>
  import IndexHeader from './pages/index-header.vue';
  import IndexBody from './pages/index-body';
  import IndexFooter from './pages/index-footer';
  import IndexLogo from './pages/index-logo'

  export default {
    data() {
      return {
        testList: [],
        account: 123,
        password: "456",
        userName: "登录/注册",
      }
    },
    name: "index",
    mounted() {
      //关联本地数据库
      this.createUserTable();
      this.booksTable();
      this.shopCarTable();
    },
    methods: {
      //创建用户名表
      createUserTable() {
        this.$db.transaction(function (tx) {
          //创建一个名字为USER的表，用户名唯一，密码，昵称，邮箱
          tx.executeSql('CREATE TABLE IF NOT EXISTS USER (account unique, password, nickName, mail)');
        }, function () {
          console.log("建USER表失败");
        });
      },
      //创建图书表
      booksTable() {
        this.$db.transaction(function (tx) {
          //创建一个名字为HOT的表，id唯一，书籍id，书籍照片，书籍名称，书籍价格,，书籍描述，书籍数量,书籍类别：1：热门精品，2：低价好物，3：9新以上
          //
          tx.executeSql('CREATE TABLE IF NOT EXISTS BOOKS(bookId INTEGER unique,bookImg, bookName,bookPrice,bookDescribe,bookCount,type)');
          tx.executeSql('INSERT INTO BOOKS(bookId,bookImg,bookName,bookPrice,bookDescribe,bookCount,type) ' +
            'VALUES (?,?,?,?,?,?,?)',[11, 'data:image/jpg;base64,','世界名人传记',20.50,'这是一本很牛逼的世界名人传记，里面详细记载了',2,1]);
          }, function () {
          console.log("建BOOKS表失败");
        });
      },
      //购物车表
      shopCarTable() {
        this.$db.transaction(function (tx) {
          //创建一个名字为SHOPCAR的表，id唯一,购物车图片，购物车书籍名字，价格
          tx.executeSql('CREATE TABLE IF NOT EXISTS SHOPCAR(shopCarId INTEGER unique,shopCarImg,shopCarPrice,shopCarName)');
        }, function () {
          console.log("建SHOPCAR表失败");
        });
      },


    },
    components: {
      IndexHeader, IndexBody, IndexFooter, IndexLogo
    }
  }
</script>

<style scoped>

</style>



